import {
    Button,
    Icon,
    Tab,
    Tabs,
    Tabbar, 
    TabbarItem,
    ShareSheet,
    ImagePreview,
    Dialog,
    NavBar,
    ActionSheet,
    Uploader,
    PullRefresh,
    List,
} from 'vant'

const UIComponents = [
    Button,
    Icon,
    Tab,
    Tabs,
    Tabbar, 
    TabbarItem,
    ShareSheet,
    ImagePreview,
    Dialog,
    NavBar,
    ActionSheet,
    Uploader,
    PullRefresh,
    List,
]


const VantUI = {};

VantUI.install = function (Vue) {
    UIComponents.forEach(Component => {
        Vue.use(Component)
    })
}

export default VantUI